<template>
  <div class="greeting">
    <h1>{{ greeting }}</h1>
    <div class="hello">{{ about }}</div>
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "Greeting",
  computed: {
    ...mapState(["language"]),
    greeting() {
      return this.language === "English"
        ? "Welcome to my portfolio page!"
        : "Добро пожаловать на мою страницу-портфолио!";
    },
    about() {
      return this.language === "English"
        ? 'My name is Alexander. On this web-site you can get some information about me. On the page, named "About", you will get info about me, my skills and experience. The ways to contact with me, you will find on the "Contacts" page. Nice to meet you! :)'
        : 'Меня зовут Александр. На этом сайте вы можете получить интересующую вас информацию обо мне. На странице "Обо мне" вы найдёте осоновную информацию обо мне, моём опыте и умениях. На странице "Контакты" представлены способы, которыми можно со мной связаться. Рад видеть вас на своём сайте. Будьте как дома! :)';
    }
  }
};
</script>

<style scoped>
.hello {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-weight: 600;
  font-style: italic;
  font-size: 18px;
  text-align: justify;
  padding: 10px;
}
</style>
