import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    language: "English"
  },
  getters: {
    language: state => state.language
  },
  mutations: {
    changeLang(state, language) {
      Vue.set(state, "language", language);
    }
  },
  actions: {},
  modules: {}
});
