<template>
  <div class="switcher" v-on:click="lang_switch">
    <div
      class="language"
      v-bind:class="language === 'Russian' ? 'activated' : ''"
    >
      <span>RU</span>
    </div>
    <div
      class="language"
      v-bind:class="language === 'English' ? 'activated' : ''"
    >
      <span>EN</span>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";

export default {
  name: "Switcher",
  methods: {
    ...mapMutations(["changeLang"]),
    lang_switch: function() {
      if (this.language === "English") {
        this.changeLang("Russian");
      } else {
        this.changeLang("English");
      }
    }
  },
  computed: mapState(["language"])
};
</script>

<style scoped>
.switcher {
  flex: 1 1 10%;
  background: #fff;
  border: 2px solid rgb(112, 111, 111);
  border-radius: 5px;
  margin: 5px;
}

.language {
  display: inline-block;
  width: 50%;
  height: 100%;
  background: rgb(153, 153, 153);
  color: #000;
  line-height: 40px;
}

.language.activated {
  background: #098609;
  color: #fff;
}
</style>
